const data = [
	{id:1, uncollapsed: true, name: 'La gran Red Verde.', description: 'La gran Red Verde. En colaboración con las asociaciones del municipio, recuperaremos las vías pecuarias, acondicionándolas con vegetación autóctona que facilite la conservación de la biodiversidad rinconera. Una red de caminos públicos que conectan de forma natural todas las ciudades y pueblos andaluces y que ha sido descuidada con el transcurso de los años. Una gran Red Verde, que también será hogar para muchas aves de paso al Doñana y otras especies propias del lugar.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación', 'Autónom@s, Pymes y Economía social', 'Cultura', 'Deporte']}, 
	{id:2, uncollapsed: false, name: 'Área de caravanas.', description: 'Área de caravanas. Con este área de aparcamientos específicos eliminaremos los obstáculos que actualmente ejercen nuestras ordenanzas municipales que prohíben el estacionamiento de este tipo de vehículos en nuestra localidad. Además, viene a reforzar nuestro sector turístico y servirá para dar a conocer nuestra gran red verde.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación']}, 
	{id:3, uncollapsed: false, name: 'Protección de los Bosques-Isla.', description: 'Protección de los Bosques-Isla. La Rinconada tiene un bosque, se encuentra cerca del polígono industrial Aerópolis, a las afueras de La Rinconada y cerca de Carmona. Es un lugar poco frecuentado por nuestras vecinas y vecinos e inserto en un espacio donde el PGOU prevé la ampliación del suelo industrial. Nuestro objetivo es protegerlo pero a la vez transformalo para hacerlo crecer. Sabemos que en otros lugares del mundo, ha sido posible combinar la actividad humana con la conservación de este tipo de bosques. Por ello, planteamos un plan que active el turismo y la creación de empleo sostenible mediante la creación de un centro de visitantes y un aula de la naturaleza.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación', 'Deporte']}, 
	{id:4, uncollapsed: false, name: 'El agua, también es nuestro medio.', description: 'El agua, también es nuestro medio. La recuperación del tramo alto del Arroyo Almonazar y de la Cañada Real del Término, será aprovechada para crear un vía verde que unirá el núcleo urbano de San José con los bosques del Arroyo Espartales, facilitando así la conexión del medio urbano con el medio rutal. En la Rinconada, buscaremos la recuperación de los usos tradicionales del Río Guadalquivir. Impulsando un plan integral de adecuación que, manteniendo el respeto al medio natural, permita el uso y disfrute del Río Guadalquivir.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación', 'Autónom@s, Pymes y Economía social', 'Deporte']}, 
	{id:5, uncollapsed: false, name: 'Renovación de nuestro compromiso con la protección medioambiental.', description: 'Renovación de nuestro compromiso con la protección medioambiental: No al fracking ni a otras prácticas destructivas de nuestro medioambiente local. La defensa de nuestro medioambiente es también la defensa de nuestras fuentes de riqueza, empleo y bienestar. Por ello, en La Rinconada no contará con ningún permiso para la realización de este tipo de actividades que pondrían en serio riesgo nuestro bienestar ambiental.',tags: ['Agricultura, Ganadería y Alimentación', 'Cultura', 'Democracia y Participación']}, 
	{id:6, uncollapsed: false, name: 'Acondicionamiento de Parques.', description: 'Acondicionamiento de Parques: El uso y disfrute de nuestros parques y zonas verdes no debe estar reñido con una adecuada dotación de servicios para nuestros animales de compañía. El “corralito” es un sinsentido antihigiénico, los animales necesitan espacio y las personas que los cuidan no los sacan para volverlos a encerrar. Por eso planteamos “zonas con animales” que se vinculen a la recuperación de zonas degradadas en parques y jardines para permitir la convivencia entre personas y animales.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación', 'Empleo', 'Democracia y Participación', 'Deporte']}, 
	{id:7, uncollapsed: false, name: 'Adecuación de Alcorques.', description: 'Adecuación de Alcorques: La ampliación y adaptación de los alcorques de los árboles facilitarán su uso natural por animales sueltos, reduciendo la incidencia de excrementos en las aceras y creando un entorno cómodo para las personas propietarias y los servicios municipales de limpieza.',tags: ['Turismo', 'Empleo', 'Animalismo', 'Cuidados']}, 
	{id:8, uncollapsed: false, name: 'Albergue de acogida de animales.', description: 'Albergue de acogida de animales: Para solucionar y atender las responsabilidades del municipio relativas a la protección de los animales de compañía, se creará un albergue municipal de recogida que seguirá el protocolo elaborado por el ICOV. Protocolo que recoge el aspecto sanitario completo de funcionamiento, organización y actuación a seguir dentro de las protectoras y refugios de pequeños animales. Con ello incrementaremos las posibilidades de otorgar una nueva vida a las mascotas abandonadas.',tags: ['Animalismo', 'Cuidados']}, 
	{id:9, uncollapsed: false, name: 'Parque animal.', description: 'Parque animal: Anexo al albergue, encontraremos un parque de uso y entrenamiento animal, acondicionado tanto para servir al albergue como al municipio.',tags: ['Empleo', 'Animalismo', 'Cuidados']}, 
	{id:10, uncollapsed: false, name: 'Recuperación de la figura del veterinario municipal.', description: 'Recuperación de la figura del veterinario municipal. Un nuevo servicio que con el que tiempo atrás contábamos todos los Rinconeros y Rinconeras. Estará a disposición de aquellas familias con escasez de recursos económicos.',tags: ['Empleo', 'Animalismo', 'Cuidados']}, 
	{id:11, uncollapsed: false, name: 'Eliminación de la pirotecnia de alta sonoridad.', description: 'Eliminación de la pirotecnia de alta sonoridad. Planteando alternativas existentes en el mercado.',tags: ['Animalismo', 'Cuidados']}, 
	{id:12, uncollapsed: false, name: 'Soderín como empresa energética municipal.', description: 'Soderín como empresa energética municipal. Consideramos que Soderín está infrautilizada, que solo se dedica a una pequeña parte de sus funciones sociales. La Rinconada se merece un nuevo modelo de desarrollo que no esté anclado al ladrillo. De esta manera, proponemos que Soderín se encargue de: La creación de una potente granja municipal de energía solar. Este proyecto se realizará por fases donde la primera persigue el autoabastecimiento de energía para los servicios municipales Promocionar las granjas solares particulares o vecinales que se autoabastezcan y aporten el sobrante energético a la red común Promoción de la adaptación energética de edificios tanto públicos como particulares y comunidades de vecinos. En colaboración con la FAMP, crear una red de empresas energéticas municipales en toda Andalucía, en beneficio de todas y todos.',tags: ['Turismo', 'Agricultura, Ganadería y Alimentación', 'Autónom@s, Pymes y Economía social', 'Empleo', 'Ciencia, innovación y desarrollo']}, 
	{id:13, uncollapsed: false, name: 'Lucha contra la pobreza y la desprotección.', description: 'Lucha contra la pobreza y la desprotección. Ningún vecino ni vecina sin luz, agua, techo ni comida',tags: ['Mayores', 'Niñ@s y adolescentes', 'Servicios públicos']}, 
	{id:14, uncollapsed: false, name: 'Potente e integradora línea de alquiler social.', description: 'Potente e integradora línea de alquiler social destinado a familias numerosas, en riesgo de exclusión social o con necesidades especiales.',tags: ['Servicios públicos']}, 
	{id:15, uncollapsed: false, name: 'No a los deshaucios de primera vivienda.', description: 'No a los deshaucios de primera vivienda. La Rinconada no consentirá dejar a ningún vecino ni vecina en la calle, sin alternativa habitacional.',tags: ['Mayores', 'Niñ@s y adolescentes', 'Servicios públicos']}, 
	{id:16, uncollapsed: false, name: 'Higiene bancaria.', description: 'Higiene bancaria. Nuestro Ayuntamiento no tratará con bancos que dejen a nuestra gente a la deriva. Adicionalmente, proponemos una auditoría de los servicios financieros para la transición hacia la banca ética. Nuestros ahorros y transacciones financieras no se invertirán en armas. ni con bancos que comercien con los Derechos Humanos.',tags: ['Banca, Sistema financiero']}, 
	{id:17, uncollapsed: false, name: 'Lucha activa contra cualquier tipo de discriminación', description: 'Lucha activa contra cualquier tipo de discriminación, ya sea por motivos de sexo, religión, orientación sexual o raza. Precisamente una sociedad se hace más fuerte con la diversidad.',tags: ['Identidad de género', 'Feminismos', 'Jóvenes']}, 
	{id:18, uncollapsed: false, name: 'Comidas a domicilio subvencionadas para personas mayores o dependientes', description: 'Comidas a domicilio subvencionadas para personas mayores o dependientes que vivan solas con un alto grado de dependencia y/o bajos ingresos. Dando la opción al que, de forma privada, quiera contratar este servicio.',tags: ['Empleo', 'Mayores', 'Servicios públicos']}, 
	{id:19, uncollapsed: false, name: 'Compromiso con los más mayores', description: 'Compromiso con los más mayores para elaborar una oferta de ocio saludable y elaborar un plan integrador que mejore las condiciones de vida y procure los recursos necesarios para mantenerse activo e independiente en esa etapa de la vida.',tags: ['Empleo', 'Mayores']}, 
	{id:20, uncollapsed: false, name: 'Bolsa de empleo transparente.', description: 'Bolsa de empleo transparente. Trabajaremos para que cualquier demandante de empleo conozca el lugar que ocupa en la lista de demandantes en cada convocatoria. Publicaremos en formato abierto y auditable, cada proceso de selección, respetando la privacidad de cada persona. Publicaremos los perfiles de los seleccionados con los datos más significativos y así dar a conocer la rotación en el empleo. Con ello podremos saber si se está haciendo un reparto justo del empleo local.',tags: ['Empleo', 'Democracia y Participación', 'Servicios públicos']}, 
	{id:21, uncollapsed: false, name: 'Apoyo a la contratación.', description: 'Apoyo a la contratación. Incentivos y ayudas especiales a empresas que contraten trabajadores desempleados o transformen contratos temporales en indefinidos.',tags: ['Empleo', 'Servicios públicos']}, 
	{id:22, uncollapsed: false, name: 'Empleo joven.', description: 'Empleo joven. Uniendo demanda a formación y necesidades, generando itinerarios que permitan una rápida inserción laboral.',tags: ['Empleo', 'Jóvenes']}, 
	{id:23, uncollapsed: false, name: 'Nuevo modelo productivo.', description: 'Nuevo modelo productivo. Desde un punto de vista integrador, pondremos en valor los recursos de los que dispone La Rinconada, de sus sectores productivos más singulares y significativos y de su ubicación privilegiada. Un sello de calidad que nos distingue. Para ello, la inversión en I+D+i, el aprovechamiento de la cadena de valor, la integración tecnológica y la no dependencia de sectores específicos serán determinantes en el desarrollo de la marca La Rinconada.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Banca, Sistema financiero', 'Ciencia, innovación y desarrollo', 'Democracia y Participación']}, 
	{id:24, uncollapsed: false, name: 'Impulso de nuevos sectores.', description: 'Impulso de nuevos sectores. La Rinconada cuenta con un capital ambiental de gran valor, además de un talento que hace de sus sectores hosteleros y cultural un referente en la provincia de Sevilla. Además, gracias a su desarrollo en infraestructuras y comunicaciones, hace de nuestro pueblo un lugar idóneo para atraer talento y presentarles una oferta, un proyecto de vida muy atractivo y singular. Sabemos que las grandes empresas también se ubicarán en ciudades donde puedan atraer talento y donde estas personas estén cómodas. Así pues, aplicaremos todos los recursos disponibles de los que dispone nuestro Ayuntamiento para generar el microclíma necesario para el desarrollo de la economía de La Rinconada.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Banca, Sistema financiero', 'Ciencia, innovación y desarrollo', 'Jóvenes']}, 
	{id:25, uncollapsed: false, name: 'Fortalecimiento del sector agroganadero.', description: 'Fortalecimiento del sector agroganadero. Apostamos por una mayor introducción de I+D+i en nuestra agroganadería y por una profunda articulación del sector. En los últimos tiempos se han venido desarrollando diferentes tecnologías que mejoran la cadena de suministro y la trazabilidad de todo el proceso productivo. Estos avances deben estar accesibles para todas y todos nuestros productores sin importar la dimensión de su empresa.',tags: ['Agricultura, Ganadería y Alimentación', 'Autónom@s, Pymes y Economía social', 'Empleo']}, 
	{id:26, uncollapsed: false, name: 'Red local de comercio municipalista.', description: 'Red local de comercio municipalista. Fomentaremos la creación de una red de colaboración entre pequeños agricultores y consumidores locales. Además de reducir la huella de carbono y mejorar la calidad final de los productos y servicios, este nuevo modelo de consumo se ha demostrado eficaz a la hora de rentabilizar la actividad agrícola.',tags: ['Agricultura, Ganadería y Alimentación', 'Empleo', 'Banca, Sistema financiero', 'Ciencia, innovación y desarrollo', 'Cuidados']}, 
	{id:27, uncollapsed: false, name: 'Recuperación del mercado de abastos.', description: 'Recuperación del mercado de abastos. La Rinconada destaca por sus infraestructura de comunicaciones y deportivas pero carece de recursos que otorguen un buen rendimiento al comercio local y de proximidad. Para mejorar estas dotaciones y con el objeto de incrementar la productividad del comercio local, recuperaremos el concepto de mercado de abastos. Lo dotaremos de suficientes recursos, como son una buena superficie, aparcamientos y una ubicación privilegiada. Además, promoveremos la incorporación de empresas de restauración para que la experiencia de compra sea un valor diferenciador. Para poder disfrutar de los servicios que presta este nuevo mercado de abastos, los productos y servicios que se presten deben estar sujetos a los principios del bien común, que no es más que, desarrollar la actividad empresarial con los métodos y hábitos éticos que caracterizan a nuestros comerciantes.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo']}, 
	{id:28, uncollapsed: false, name: 'Medio natural como atractivo turístico y sector emergente.', description: 'Medio natural como atractivo turístico y sector emergente. Nuestro medio natural ha sido descuidado durante años. Recuperaremos todo su valor y lo pondremos a disposición de las y los rinconeros, para darlo a conocer también por la capital y toda La Vega. Facilitando así, la implantación de empresas del sector en nuestro municipio',tags: ['Autónom@s, Pymes y Economía social', 'Empleo']}, 
	{id:29, uncollapsed: false, name: 'Suelo libre de prácticas de agricultura industrial.', description: 'Suelo libre de prácticas de agricultura industrial. La agricultura ecológica se ha demostrado como sostenible y rentable pero solo si se le otorga los recursos de los que necesita. Para ello, facilitaremos una buena extensión de nuestro suelo agrícola sobre la que evitaremos que se realicen prácticas como la aplicación de venenos y productos químicos, consiguiendo protejer nuestra capa fertil que este se convierta en un atractivo para la implantación de la agricultura ecológica en nuestro municipio. Irá acompañado de una guía de interpretación, señalización de la zona y acciones para su difusión entre la ciudadanía rinconera.',tags: ['Agricultura, Ganadería y Alimentación', 'Autónom@s, Pymes y Economía social', 'Servicios públicos']}, 
	{id:30, uncollapsed: false, name: 'Adecuación de Polígonos industriales.', description: 'Adecuación de Polígonos industriales. Impulsaremos la mejora de la escena urbana de los polígonos industriales y los dotaremos de nuevas infraestructuras para aprovechar mejor la ubicación de los mismos. Una estrategia para modernizar el concepto de polígono industrial que se integrará en esta marca “La Rinconada” que diferencie perfectamente lo que se produce aquí.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Servicios públicos']}, 
	{id:31, uncollapsed: false, name: 'Colaboración con entidades sociales de apoyo al microcrédito.', description: 'Colaboración con entidades sociales de apoyo al microcrédito. Promocionaremos y firmaremos acuerdos con entidades dispuestas a apoyar un soporte financiero básico para vertebrar líneas de microcréditos. Será una vía de financiación amigable y socialmente comprometida con la generación de oportunidades sin necesidad de poner en riesgo los bienes básicos como pueda ser el hogar familiar.',tags: ['Banca, Sistema financiero', 'Servicios públicos']}, 
	{id:32, uncollapsed: false, name: 'Cámara independiente de innovación financiera y empresarial de La Rinconada.', description: 'Cámara independiente de innovación financiera y empresarial de La Rinconada. Creemos que la economía social y los principios de la economía del bien común han servido como palancas de transformación de las sociedades que así lo han necesitado. Andalucía cuenta, además con una de las leyes más avanzadas en este término. Con estos ingredientes básicos, unidos a la capacidad de nuestro Ayuntamiento en todo lo que a comunicación se refiere, fomentaremos la consolidación de una red de organizaciones y personas independientes para la innovación y desarrollo de la economía rinconera. Los objetivos que perseguirán son: La aplicación de los avances científicos y técnicos y su puesta en marcha por las empresas de La Rinconada La promoción y consolidación del micromecenazgo privado (financiación a pequeña escala) con criterios sociales alejados de la especulación y El desarrollo de nuevas líneas estratégicas que servirán a los actuales servicios de desarrollo y empleo del Ayuntamiento de La Rinconada para aprovechar mejor los recursos públicos de los que se dispone actualmente. Con todo ello superaremos ese postmoderno e ineficaz concepto del autoempleo aislado e individual por un concepto de emprendimiento social y colaborativo y con conocimiento experto.',tags: ['Autónom@s, Pymes y Economía social', 'Jóvenes', 'Servicios públicos']}, 
	{id:33, uncollapsed: false, name: 'Cámara independiente de innovación en tecnologías.', description: 'Cámara independiente de innovación en tecnologías de la información y la comunicación de La Rinconada. En los últimos tiempos hemos sido partícipes de un giro en las relaciones sociales con el desarrollo de las tecnologías de la información y comunicación. Nos referimos al desarrollo de Internet y los Smartphones y otras tecnologías como las las redes sociales, por ejemplo. Estas nuevas formas de relacionarnos han transformado la sociedad al completo y nos ha obligado a empresas y particulares a adquirir nuevas habilidades para conseguir nuestros objetivos. Pero, en este sentido, no todo ha significado mejoras para la sociedad y en muchos casos, la aplicación práctica y concreta para la micropyme no ha sido completa ni accesible. Esto significa que, aunque la pequeña empresa ha sabido aprovechar esta revolución, las grandes son las que ponen a su disposición el máximo potencial que estas herramientas ofrecen. Y es por eso, por lo que vamos a promover y fomentar con líneas de actuación específicas, la consolidación de una red de organizaciones y personas independientes, con conocimiento experto en esta temática, que siguiendo los principios del software libre como son la colaboración y apertura del conocimiento, sirvan para implantar métodos y tecnologías adaptadas a la pequeña empresa. Gracias al uso de software de fuentes abiertas, esta nueva red podrá adaptar la tecnología inicialmente desarrollada para la gran empresa o con alto conocimiento en la materia y la adaptará para la empresa local de La Rinconada.',tags: ['Autónom@s, Pymes y Economía social', 'Jóvenes', 'Servicios públicos']}, 
	{id:34, uncollapsed: false, name: 'Plan de innovación municipal.', description: 'Plan de innovación municipal. Participativo y elaborado con la colaboración de asociaciones y empresas locales. Este plan director recogerá las necesidades y oportunidades detectadas en nuestro municipio. Para afrontar los problemas del presente hay que elevar la vista hacia el futuro y aterrizar sobre lo concreto las mejores medidas que se pueden adaptar para mejorar La Rinconada. Será un documento público que servirá a empresas y administraciones como banco de ideas para el surgimiento de soluciones para el corto y medio plazo.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Banca, Sistema financiero', 'Jóvenes', 'Servicios públicos']}, 
	{id:35, uncollapsed: false, name: 'Estrategia comunicativa para el talento local.', description: 'Estrategia comunicativa para el talento local. En los últimos tiempos hemos sido testigos de como las grandes empresas, y en especial las digitales, han reducido sus necesidades de espacio. Esto explica en parte, el vacío que se ha generado en las grandes superficies de los suelos industriales. Además, la transformación de la empresa nacional, los sectores a los que atienden y de los que se nutren, han puesto la vista en nuevos modelos de negocio alejados de las grandes fábricas de antaño. Dado que uno de los recursos más demandados en estos nuevos modelos de negocio es el talento, hemos de construir toda una estrategia comunicativa y de fomento del talento local para conseguir que La Rinconada se convierta en la opción más viable para construir un proyecto de vida confortable para estas personas.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Jóvenes']}, 
	{id:36, uncollapsed: false, name: 'Compromiso para el empleo de las y los rinconeros.', description: 'Compromiso para el empleo de las y los rinconeros. La totalidad de la dotación de infraestructuras municipales dedicadas a la empresa, como son polígonos, servicios, infraestructuras, ayudas e impuestos quedan vinculadas a la creación de empleo en La Rinconada. Buscaremos activamente la implantación de empresas de alta capacidad sinérgica con el territorio o capaces de generar gran número de puestos de trabajo.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Jóvenes', 'Servicios públicos']}, 
	{id:37, uncollapsed: false, name: 'Comercio local, comercio con futuro.', description: 'Comercio local, comercio con futuro. Conocedores de las grandes ventajas que ofrece este comercio, no solo por la calidad de sus productos y servicios, sino también por las posibilidades que ofrece para mejorar el empleo local, estaremos en continua escucha, tanto del sector local como de las buenas prácticas que surjan de otras ciudades. También fomentaremos los beneficios que aporta la economía del bien común a la hora de construir la marca “La Rinconada” y aportaremos nuevos servicios derivados de la buena marcha de esta implantación, con el objetivo de vincular al cliente final y superar los obstáculos que actualmente existen en este tipo de comercio. Estos servicios estarán vinculados a la publicidad y la logística principalmente y serán completados y adaptados a la necesidad del sector local de comercio. Se creará también un catálogo municipal de bienes y servicios prestados por las empresas locales que sirva de nexo entre empresas y vecinas y vecinos.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo']}, 
	{id:38, uncollapsed: false, name: 'Impulso del emprendimiento local.', description: 'Impulso del emprendimiento local. Unidas a las medidas de financiación y mejora tecnológica y guiadas por los principios de colaboración y apertura, promoveremos la ampliación de las dotaciones actuales como son el vivero de empresas y los recursos asociados. Superaremos el concepto de emprendimiento tradicional y para ello crearemos espacios dispuestos para el fomento del coworking. Una mirada integradora de los recursos, productos y servicios que actualmente dispone La Rinconada con el objetivo de tejer nuevos modelos de negocio en permanente contacto con el tejido empresarial local.',tags: ['Autónom@s, Pymes y Economía social', 'Empleo', 'Jóvenes']}, 
	{id:39, uncollapsed: false, name: 'Medidas concretas para la promoción del talento local.', description: 'Medidas concretas para la promoción del talento local. De nada serviría toda estrategia de impulso al desarrollo y emprendimiento local si no somos capaces de conseguir que nuestra ciudad sea la opción más ventajosa para el modo de vida que demandan los y las trabajadoras del siglo XXI. Para ello adaptaremos nuestras ordenanzas fiscales que rigen los precios públicos de estos nuevos servicios municipales para que sean ventajosas para los empadronados en La Rinconada. Además, otorgaremos ayudas y subvenciones para material escolar y servicios educativos para “trabajadores en remoto” que vivan en nuestro municipio. Trabajaremos en materia de Cultura y Comercio para crear una oferta, un modo de vida, que distinga a La Rinconada por saber adaptar su ciudad a las demandas de vecinas y vecinos.',tags: ['Empleo', 'Jóvenes']}, 
	{id:40, uncollapsed: false, name: 'Creación de la oficina de acceso a mercados e internacionalización.', description: 'Creación de la oficina de acceso a mercados e internacionalización. Destinada a apoyar a nuestras empresas, agricultores y ganaderos locales en la comercialización de sus productos, tanto en territorio nacional como internacional.',tags: ['Autónom@s, Pymes y Economía social']}, 
	{id:41, uncollapsed: false, name: 'Compromiso de práctica responsable.', description: 'Compromiso de práctica responsable. Eliminaremos los coches oficiales, los chóferes particulares y las prebendas. Los representantes públicos tendrán los mismos derechos que el resto de trabajadores del ayuntamiento, ni uno más.',tags: ['Democracia y Participación']}, 
	{id:42, uncollapsed: false, name: 'Control y vigilancia.', description: 'Control y vigilancia. Agenda pública y exhaustiva de toda la actividad de nuestros representantes durante su jornada laboral.',tags: ['Democracia y Participación']}, 
	{id:43, uncollapsed: false, name: 'Obligación de publicidad.', description: 'Obligación de publicidad. Declaración de renta y patrimonio durante los años de mandato.',tags: ['Democracia y Participación']}, 
	{id:44, uncollapsed: false, name: 'Limitación de sueldos.', description: 'Limitación de sueldos. El salario de alcalde y concejales estará limitado a tres veces el salario mínimo interprofesional, salvo para aquellos que ya tengan un sueldo consolidado del sector profesional del que proceda.',tags: ['Democracia y Participación']}, 
	{id:45, uncollapsed: false, name: 'Horquilla salarial.', description: 'Horquilla salarial. Fijación de un límite en la horquilla salarial entre empleado público peor pagado y político mejor pagado.',tags: ['Democracia y Participación']}, 
	{id:46, uncollapsed: false, name: 'Portal de transparencia.', description: 'Portal de transparencia. Renovación del portal de transparencia para dotarlo de un sistema de búsqueda inteligente, con la información mejor organizada y sobre todo mejor explicada. Porque lo público también necesitamos explicarlo mejor.',tags: ['Democracia y Participación']}, 
	{id:47, uncollapsed: false, name: 'Revocabilidad.', description: 'Revocabilidad. Todos los cargos electos podrán ser revocados si la ciudadanía así lo decide. Impulsaremos la creación de un protocolo nítido que recoja los métodos y formas para proceder.',tags: ['Democracia y Participación']}, 
	{id:48, uncollapsed: false, name: 'Referéndum económico.', description: 'Referéndum económico. Obligación de someter a aprobación popular toda obra o gasto que supere un porcentaje concreto del presupuesto anual disponible (descontados gastos fijos como personal o mantenimiento). Las grandes obras e inversiones deben contar con el consenso ciudadano.',tags: ['Democracia y Participación']}, 
	{id:49, uncollapsed: false, name: 'Rendición pública de cuentas.', description: 'Rendición pública de cuentas. Todos los gastos del Ayuntamiento serán públicos, actualizados inmediatamente por cuenta corriente y concepto, en formato abierto y disponible para su explotación por terceros.',tags: ['Democracia y Participación']}, 
	{id:50, uncollapsed: false, name: 'Procesos públicos de contratación.', description: 'Procesos públicos de contratación. Los procesos serán públicos, filmados y sus reuniones retransmitidas online y de libre y pública asistencia. La totalidad de la documentación aportada será pública así como las posibles decisiones y votos particulares.',tags: ['Autónom@s, Pymes y Economía social', 'Democracia y Participación']}, 
	{id:51, uncollapsed: false, name: 'Incompatibilidad de cargos.', description: 'Incompatibilidad de cargos. La dedicación para los cargos liberados será exclusiva, no pudiendo simultanearse más de un puesto remunerado al mismo tiempo. Los miembros no liberados del consejo de gobierno tendrán derecho a recibir retribución proporcional a la que pudiera perderse en el puesto de trabajo por la asistencia a pleno o tareas propias de su actividad.',tags: ['Democracia y Participación']}, 
	{id:52, uncollapsed: false, name: 'Limitación de mandatos.', description: 'Limitación de mandatos. Limitado a 8 años con posibilidad de ampliación a 12 bajo aceptación popular regulada.',tags: ['Democracia y Participación']}, 
	{id:53, uncollapsed: false, name: 'Eliminación de puertas giratorias.', description: 'Eliminación de puertas giratorias. Prohibición de ocupar cargo directivo en empresas que hayan contratado con el Ayuntamiento.',tags: ['Democracia y Participación']}, 
	{id:54, uncollapsed: false, name: 'Auditorías externas.', description: 'Auditorías externas. Sus resultados serán públicos y didácticos.',tags: ['Democracia y Participación']}, 
	{id:55, uncollapsed: false, name: 'Derecho de auditoría ciudadana.', description: 'Derecho de auditoría ciudadana. Podrán solicitar una auditoría sobre las cuentas del ayuntamiento o sus empresas públicas.',tags: ['Democracia y Participación']}, 
	{id:56, uncollapsed: false, name: 'Curso de prevención de la corrupción.', description: 'Curso de prevención de la corrupción. Obligatorio para todos los cargos públicos y funcionarios con altas responsabilidades.',tags: ['Democracia y Participación', 'Servicios públicos']}, 
	{id:57, uncollapsed: false, name: 'Campañas de información a la ciudadanía.', description: 'Campañas de información a la ciudadanía. Sobre derechos de vigilancia y control de la administración pública y formas de hacerlos efectivos.',tags: ['Democracia y Participación', 'Servicios públicos']}, 
	{id:58, uncollapsed: false, name: 'Simplificación burocrática.', description: 'Simplificación de trámites a fin de reducir al máximo tanto el número de procesos, como el tiempo en la gestión.',tags: ['Servicios públicos']}, 
	{id:59, uncollapsed: false, name: 'Mediador burocrático.', description: 'Mediador burocrático. Una referencia única que acompaña y ayuda al usuario en su relación con la administración municipal. Basta de viajar de ventanilla en ventanilla.',tags: ['Servicios públicos']}, 
	{id:60, uncollapsed: false, name: 'Presupuestos abiertos.', description: 'Presupuestos abiertos. Toda la información presupuestaria será publicada en formato abierto y accesible y de forma bien organizada. Con una buena estructuración de cada partida y con una explicación de cada concepto. Se hará especial énfasis en los medios técnicos que faciliten la búsqueda de información económica en este presupuesto. Contendrá, además, toda la información relativa a la ejecución de cada partida presupuestaria y una sección dispuesta para que cada vecino y vecina pueda realizar las consultas que estimen oportunas. El plazo de resolución será el necesario para que esta herramienta no quede en desuso.',tags: ['Democracia y Participación']}, 
	{id:61, uncollapsed: false, name: 'Presupuestos participativos.', description: 'Presupuestos participativos. La ciudadanía tiene derecho a construir el mundo en el que vive. Con la dotación de Presupuestos participativos queremos abrir vías al margen del dominio clientelar de las instituciones públicas para que la sociedad civil se desarrolle, de manera libre y acorde con los deseos de nuestra gente. Por eso será el pueblo, a través de los presupuestos abiertos y participativos, el que defina buena parte de las actuaciones, proyectos y acciones que habrán de desarrollarse en nuestro municipio por parte, tanto de asociaciones como de personas o colectivos.',tags: ['Democracia y Participación']}, 
	{id:62, uncollapsed: false, name: 'DemoLocal.', description: 'DemoLocal. Con la DemoLocal, cualquier vecino o vecina podrá recuperar el poder entregado a un cargo electo para ejercerlo directamente en el Pleno Municipal, con un peso proporcional a su voto en las elecciones.',tags: ['Democracia y Participación']}, 
	{id:63, uncollapsed: false, name: 'Iniciativas normativas populares.', description: 'Iniciativas normativas populares. La INP será otro de los nuevos derechos que habremos de reconocer, el derecho de los barrios y los distritos a regular aspectos esenciales de la vida diaria, el derecho de nuestra gente a elevar al pleno políticas, reglamentos y normas de aplicación municipal o territorial. Con este mecanismo devolvemos a la ciudadanía su derecho a gobernarse así misma, a construir su presente.',tags: ['Democracia y Participación']}, 
	{id:64, uncollapsed: false, name: 'Plan participativo de desarrollo humano sostenible.', description: 'Plan participativo de desarrollo humano sostenible. El futuro de un pueblo no se define en unos meses, su proyección y ejecución tampoco estarán sujetos a la cita anual con la aprobación de los presupuestos. Debe ser un proyecto a años vista y que impregne la vida diaria de La Rinconada. El modelo de desarrollo que adoptemos deberá refrendarse con la implicación de todas las personas y sectores del municipio. En este plan se recogerán los principales problemas y retos de La Rinconada con el planteamiento de soluciones recogidas en procesos participativos barriales y de distritos. Este plan será público y reutilizable y será la hoja de ruta para cualquier gobierno municipal. Nuestros representantes serán la herramienta, le darán voz en las instituciones pero de nuevo, las decisiones más importantes no se toman cada 4 años, la confianza debe ser refrendada cada día.',tags: ['Democracia y Participación']}, 
	{id:65, uncollapsed: false, name: 'Constitución municipal.', description: 'Constitución municipal. Todos los mecanismos que implantemos en nuestro mandato, los nuevos derechos reconocidos y los procesos de control de la función pública han de ser recogidos y reconocidos en un documento de consenso que deberán respetar los gobiernos venideros. La actual situación de incertidumbre respecto a los derechos conquistados en el pasado debe ser corregida mediante modelos de gestión que priorice el consenso en lo público. Nunca más una sociedad que para avanzar sacrifique los derechos de los más débiles.',tags: ['Democracia y Participación']}, 
	{id:66, uncollapsed: false, name: 'Nuevo concepto en el consejo de participación ciudadana.', description: 'Nuevo concepto en el consejo de participación ciudadana. Para que la participación ciudadana sea más efectiva, necesita de una mejora de los procedimientos burocráticos y la dotación de los medios y servicios técnicos adecuados. El consejo municipal de participación ciudadana será convocado trimestralmente para la dación de cuentas de la gestión municipal, con participación de todos los grupos de la oposición y donde la ciudadanía sea la protagonista. No puede ser que en un consejo de participación ciudadana se escuche más a los políticos responsables de la gestión municipal que a las propias vecinas y vecinos. Este consejo de participación será más propositivo y consultivo a la vez que diverso. Estará mejor informado y se le otorgarán las distintas alternativas que se planteen por los diferentes grupos políticos de La Rinconada, en unos plazos adecuados y con un lenguaje no técnico al alcance de todos y todas.',tags: ['Democracia y Participación', 'Servicios públicos']}, 
	{id:67, uncollapsed: false, name: 'Observatorio del Asociacionismo independiente.', description: 'Observatorio del Asociacionismo independiente. Es fácil confundir el fomento del asociacionismo con la intervención política en los consejos de las asociaciones. Por ello planteamos el nombramiento de un observatorio de asociaciones independiente, sin intervención política, cuyo objetivo sea velar por la independencia en el terreno asociacionista de La Rinconada y fomentar la creación y diversificación del tejido asociativo local. Para el correcto funcionamiento de este observatorio, se nombrará a un técnico de los servicios municipales que se encargue del enlace entre administración y el observatorio local. Este cargo es meramente técnico y se ocupará de sus labores con posiblidad de renovar su cargo anualmente. Este nombramiento se realizará en el primer trimestre de la legislatura y contará con la participación del máximo número de asociaciones.',tags: ['Democracia y Participación']}, 
	{id:68, uncollapsed: false, name: 'Código ético y de buen gobierno, también en las redes sociales.', description: 'Código ético y de buen gobierno, también en las redes sociales. Las redes sociales se han convertido también en un espacio público que debemos cuidar. Procuraremos elaborar un código ético y de buen gobierno digital para con nuestras vecinas y vecinos. Basta de injerencias en los grupos de las redes sociales. Una sociedad que se considera libre también puede opinar respecto de la gestión de lo público.',tags: ['Democracia y Participación']}, 
	{id:69, uncollapsed: false, name: 'Auditorio callejero municipal.', description: 'Auditorio callejero municipal para que podamos hacer libre expresión artística en directo. El arte urbano debe convertirse en una línea estratégica de innovación cultural.',tags: ['Cultura', 'Jóvenes', 'Niñ@s y adolescentes', 'Servicios públicos']}, 
	{id:70, uncollapsed: false, name: 'Impulso del asociacionismo libre e independiente.', description: 'Impulso del asociacionismo libre e independiente. Nos comprometemos con un modelo de asociacionismo libre de ataduras políticas, económicamente sostenible y socialmente responsable. Por ese motivo, los presupuestos participativos permitirán a la ciudadanía decidir qué proyectos y asociaciones gozan en cada momento de financiación pública. Cuidaremos siempre de proporcionar especial atención a aquellas entidades que, con carácter altruista, vienen desarrollando funciones humanitarias, culturales o sociales en La Rinconada desde hace años y vertebran nuestra vida social.',tags: ['Cultura', 'Democracia y Participación']}, 
	{id:71, uncollapsed: false, name: 'Consejo cultural ciudadano.', description: 'Consejo cultural ciudadano. Constituido por artistas, aficionados, asociaciones, empresas y organizaciones municipales ligadas al fomento y creación de la cultura. Su misión principal será colaborar en la redacción del plan anual del área de cultura, valorar el funcionamiento del modelo de gestión cultural, proponer opciones de mejora y líneas de trabajo futuras.',tags: ['Democracia y Participación']}, 
	{id:72, uncollapsed: false, name: 'El Carnaval con identidad local.', description: 'El Carnaval con identidad local. En colaboración con las agrupaciones locales, elaboraremos un plan para fomentar esta identidad tan andaluza. Mejoraremos nuestros espacios públicos y dotacionales para la adaptación a este modo de expresión y seremos sensibles a las demandas del sector para mejorar nuestro carnaval local.',tags: ['Cultura', 'Democracia y Participación']}, 
	{id:73, uncollapsed: false, name: 'Memoria histórica.', description: 'Memoria histórica. Una sociedad que avanza es aquella que se reconcilia con su pasado. Articularemos los medios necesarios para que la memoria histórica ocupe el lugar que se merece. Crearemos un lugar de recuerdo con todos los nombres de las víctimas.',tags: ['Cultura', 'Democracia y Participación']}, 
	{id:74, uncollapsed: false, name: 'Cultura al alcance de todas y todos.', description: 'Cultura al alcance de todas y todos. La cultura debe ser disfrutada por toda la ciudadanía, para ello proponemos crear el bono cultural que facilite el disfrute de todas las formas de expresión cultural que tengan cabida en La Rinconada.',tags: ['Cultura', 'Democracia y Participación']}, 
	{id:75, uncollapsed: false, name: 'El rincón de las letras.', description: 'El rincón de las letras. La novela, la poesía, el guión cinematográfico, entre otras expresiones, tendrán cabida en el nuevo rincón de las letras. Un espacio cultural y de intercambio que fomentará la creación cultural independiente de La Rinconada.',tags: ['Cultura', 'Jóvenes', 'Mayores']}, 
	{id:76, uncollapsed: false, name: 'Arte sacro y cofrade.', description: 'Arte sacro y cofrade. Impulsaremos la creación de un archivo histórico con el que situar a esta forma de expresión tan nuestra como es el arte sacro y cofrade.',tags: ['Cultura']}, 
	{id:77, uncollapsed: false, name: 'Adaptación y ampliación del skate-park.', description: 'Adaptación y ampliación del skate-park. Con el apoyo y colaboración de personas usuarias, crearemos un plan para mejorar, ampliar y adaptar el skate-park y que sirva para diversificar las actividades que allí se practican. Un espacio ideal para el desarrollo de arte urbano, que unido a nuevas prácticas deportivas como la calistenia o el parkour, puedan surgir nuevos espacios alternativos y gratuitos para el ocio y disfrute juvenil. Trabajaremos en esta línea para ir eliminando el copago deportivo progresivamente. Este plan irá acompañado de una dotación especial para dar visibilidad a este espacio y una mejora en la conexión con los núcleos poblacionales.',tags: ['Deporte', 'Jóvenes', 'Niñ@s y adolescentes']}, 
	{id:78, uncollapsed: false, name: 'Recuperación del entorno natural para la práctica deportiva.', description: 'Recuperación del entorno natural para la práctica deportiva. En colaboración con el área de medioambiente y con las asociaciones de nuestra localidad, recuperaremos los espacios públicos como son los caminos y veredas y los adaptaremos para la práctica deportiva integrada en la naturaleza. La práctica del senderismo, cross, cicloturismo o simplemente darse un paseo por el nuevo entorno natural recuperado, serán sellos diferenciadores que harán las delicias de nuestros deportistas y serán un reclamo para mejorar el sector turístico de la localidad. Además, gracias a la conexión con otras localidades, el deporte en el entorno natural toma una nueva dimensión.',tags: ['Autónom@s, Pymes y Economía social', 'Cultura', 'Deporte']}, 
	{id:79, uncollapsed: false, name: 'Acondicionamiento del Guadalquivir.', description: 'Acondicionamiento del margen del Guadalquivir para la práctica de la pesca, el remo o la vela. El Guadalquivir también es nuestro río y debemos de cuidarlo.',tags: ['Deporte']}, 
	{id:80, uncollapsed: false, name: 'Consejo deportivo ciudadano.', description: 'Creación del consejo deportivo municipal, constituido por usuarios, clubes, empresas y organizaciones locales. Su misión principal será colaborar en la redacción del plan anual del área de deportes, valorar el funcionamiento del modelo de gestión deportiva, proponer opciones de mejora y líneas de trabajo futuras.',tags: ['Deporte']}, 
	{id:81, uncollapsed: false, name: 'Creación de un mapa/bolsa de cuidados.', description: 'Creación de un mapa/bolsa de cuidados. Se trata de crear una red de confianza formada por familias que intercambien su tiempo de cuidado de hijos; red de apoyo en la que las familias se inscriben con el tiempo del que disponen, número de niños y edades y actividades a realizar en ese tiempo. La finalidad es liberar durante una hora o dos como mucho a las madres o padres.',tags: ['Cuidados', 'Feminismos']}, 
	{id:82, uncollapsed: false, name: 'Ludotéca municipal en vacaciones.', description: 'Creación de una ludoteca municipal que cubra los festivos y mes de agosto que no cubren las guarderías y colegios.',tags: ['Cuidados', 'Feminismos']}, 
	{id:83, uncollapsed: false, name: 'Estudio para la gestión directa de los servicios de cuidados.', description: 'Estudio para la gestión directa de los servicios de cuidados. Servicios como la ayuda a domicilio, actualmente están siendo explotados por grandes multinacionales. Creemos que estos servicios pueden ser prestados directamente por nuestro ayuntamiento y con un mejor rendimiento. Además, gracias al proceso de municipalización del servicio, todas las dotaciones podrán ser aprovechadas para reducir la factura de los servicios de otros departamentos y que actualmente, también son prestadas por empresas privadas.',tags: ['Servicios públicos']}, 
	{id:84, uncollapsed: false, name: 'Plan integral de cuidado para parques, calles y jardines.', description: 'Plan integral de cuidado para parques, calles y jardines. Una Rinconada que promueve el cuidado de su vecindario, es una Rinconada que cuida también de la escena pública. Estudiaremos cada parque, cada jardín y cada calle donde hacen vida nuestras vecinas y vecinos. Es necesaria la creación de más espacios con sombra en el parque de las graveras, por ejemplo, o el incremento de papeleras en las calles más concurridas. O la dotación de baños públicos para que nuestro vecindario no se vea obligado a utilizar los servicios privados de bares y negocios.',tags: ['Empleo', 'Democracia y Participación']}, 
	{id:85, uncollapsed: false, name: 'Trabajaremos por la conciliación familiar.', description: 'Trabajaremos por la conciliación familiar. Elaborando planes locales para su promoción e impulso.',tags: ['Cuidados', 'Feminismos', 'Mayores']}, 
	{id:86, uncollapsed: false, name: 'Pacto por la lactancia materna.', description: 'Pacto por la lactancia materna. Promoveremos medidas especialmente diseñadas para el cuidado de bebés lactantes. Mejoraremos la escena urbana y elaboraremos itinerarios formativos tanto para padres y madres como para la ciudadanía en general. La lactancia materna es la forma natural de alimentar al bebé, por ello, La Rinconada debe favorecer este concepto de maternidad.',tags: ['Cuidados']}, 
	{id:87, uncollapsed: false, name: 'Banco de leche materna.', description: 'Banco de leche materna. En colaboración con otros pueblos de La Vega y junto a las asociaciones locales pro lactancia, impulsaremos las medidas necesarias para fortalecer la red de apoyo al banco de leche materna de Sevilla. Trabajaremos para facilitar el transporte y garantizar la cadena de frío.',tags: ['Cuidados']}, 
	{id:88, uncollapsed: false, name: 'Programas de sensibilización de identidad sexual.', description: 'Programas de sensibilización de identidad sexual. Los derechos relacionados con la identidad sexual son los grandes desconocidos. Proponemos un plan integral de formación para el cuerpo técnico del Ayuntamiento (Policía local y funcionarios en general), así como, uno específico para la ciudadanía en general. Será programas en constante renovación y que abarcan todo el año.',tags: ['Democracia y Participación', 'Identidad de género']}, 
	{id:89, uncollapsed: false, name: 'Incremento de la dotación para el punto visible.', description: 'Incremento de la dotación para el punto visible. Para que no haya citas sin respuesta porque todas las consultas deben ser resueltas a tiempo y porque cada vez son más las personas valientes que se atreven a plantar cara a los problemas derivados del no respeto a la identidad sexual. Proponemos un incremento en la dotación de recursos técnicos y humanos para el punto visible y una mejora en la difusión de los servicios que aporta.',tags: ['Democracia y Participación', 'Identidad de género']}, 
	{id:90, uncollapsed: false, name: 'Identidad sexual y escuela.', description: 'Identidad sexual y escuela. La formación es uno de los pilares esenciales para desterrar los miedos derivados de la identidad sexual y su problemática. Consideramos, que es esencial normalizar la cuestión de esta identidad y por eso proponemos la adaptación de los materiales para que puedan ser asimilados por los más jóvenes.',tags: ['Democracia y Participación', 'Identidad de género']}, 
	{id:91, uncollapsed: false, name: 'Inserción laboral específica.', description: 'Inserción laboral específica. Para las personas LGTBI+',tags: ['Democracia y Participación', 'Identidad de género']}, 
	{id:92, uncollapsed: false, name: 'Plan de formación para la compra sostenible.', description: 'Plan de formación para la compra sostenible. La sociedad avanza y con ella los hábitos de consumo. Por tanto, se hace necesario conocer las distintas opciones que ofrece el mercado. Para ello proponemos un plan específico de formación para los responsables de aprovisionamiento del Ayuntamiento en esta temática.',tags: ['Autónom@s, Pymes y Economía social', 'Servicios públicos']}, 
	{id:93, uncollapsed: false, name: 'Publicidad para los servicios municipales de formación.', description: 'Publicidad para los servicios municipales de formación. Existen diversos recursos formativos de los que dispone La Rinconada, desde dependencias municipales específicas, hasta asociaciones, que libremente aportan su conocimiento para dar a conocer un amplio abanico de posibilidades formativas. Se trata de organizar toda esta información para que esté siempre disponible y accesible al público.',tags: ['Servicios públicos']}, 
	{id:94, uncollapsed: false, name: 'Plan de educación afectivo-sexual.', description: 'Plan de educación afectivo-sexual.',tags: ['Democracia y Participación', 'Identidad de género']}, 
	{id:95, uncollapsed: false, name: 'Mejora de las dotaciones y servicios prestados a las AMPAS.', description: 'Mejora de las dotaciones y servicios prestados a las AMPAS.',tags: ['Democracia y Participación', 'Servicios públicos']}, 
	{id:96, uncollapsed: false, name: 'Formación obligatoria en identidad de género.', description: 'Obligatoriedad de seguir el plan formativo en igualdad y de identidad de género a los técnicos de la casa.',tags: ['Identidad de género', 'Servicios públicos']}, 
	{id:97, uncollapsed: false, name: 'Plan especial de formación para el empleo joven en la agricultura.', description: 'Plan especial de formación para el empleo joven en la agricultura.',tags: ['Agricultura, Ganadería y Alimentación', 'Empleo', 'Servicios públicos']}, 
	{id:98, uncollapsed: false, name: 'Elaboración de líneas estratégicas de formación.', description: 'Elaboración de líneas estratégicas de formación. En colaboración con escuelas, ludotecas, institutos, universidades y empresas dedicadas al sector de la enseñanza, elaboraremos un plan de formación que junto a nuestro plan de empleo y las diferentes líneas de innovación y cultura, La Rinconada sea conocida como la ciudad del conocimiento. Este plan director será público y servirá de inspiración para nuevos itinerarios formativos alternativos.',tags: ['Jóvenes', 'Servicios públicos']}, 
	{id:99, uncollapsed: false, name: 'Promoción de las Master Class en diferentes ámbitos.', description: 'Promoción de las Master Class en diferentes ámbitos. En colaboración con los distintos sectores de la formación, elaboraremos un atractivo plantel de profesionales de reconocido prestigio para que impartan sus conocimientos en nuestro municipio.',tags: ['Jóvenes', 'Servicios públicos']}, 
	{id:100, uncollapsed: false, name: 'Adecuación de los edificios públicos.', description: 'Adecuación de los edificios públicos. Aprovechamiento de todos los edificios municipales para la obtención de energías limpias.',tags: ['Banca, Sistema financiero', 'Servicios públicos']}, 
	{id:101, uncollapsed: false, name: 'Tráfico.', description: 'Tráfico. En colaboración con los vecinos y vecinas de cada zona, adecuamos buena parte del trazado urbano. Repintando, eliminando badenes innecesarios sustituyendolos por soluciones actuales para la regulación de la velocidad, modificando señalizaciones peligrosamente dispuestas o reposicionando contenedores.',tags: ['Servicios públicos']}, 
	{id:102, uncollapsed: false, name: 'Carril bici.', description: 'Carril bici. Ampliación del carril bici para hacerlo llegar a todos los núcleos diseminados. El trazado actual deberá conectar las diferentes dependencias municipales para convertir a la bicicleta en una alternativa viable de locomoción.',tags: ['Agricultura, Ganadería y Alimentación', 'Servicios públicos']}, 
	{id:103, uncollapsed: false, name: 'Modelo de urbanismo participativo.', description: 'Protagonismo vecinal para la toma de decisiones en lo que a su barrio o distrito se refiere. El modelo de urbanismo de La Rinconada debe descansar sobre los pilares de información y participación del modelo de ciudad que quieren las Rinconeras y Rinconeros.',tags: ['Democracia y Participación', 'Servicios públicos']}, 
	{id:104, uncollapsed: false, name: 'Eliminación de la infravivienda con el observatorio continuo de la vivienda.', description: 'Eliminación de la infravivienda con el observatorio continuo de la vivienda.',tags: ['Servicios públicos']}, 
	{id:105, uncollapsed: false, name: 'Vivienda intergeneracional.', description: 'Favoreceremos el concepto de vivienda intergeneracional para que nuestros mayores continúen siendo independientes y brindaremos nuevas oportunidades a los más jóvenes para comenzar un nuevo proyecto de vida en nuestro municipio. En nuestra política de vivienda pública incluiremos medidas que vayan en esa dirección creando las condiciones necesarias para poner en contacto a miembros de ambas generaciones.',tags: ['Jóvenes', 'Mayores', 'Servicios públicos']}, 
	{id:106, uncollapsed: false, name: 'Ayudas para elevadores.', description: 'En colaboración con las distintas administraciones del estado, trabajaremos para conseguir líneas presupuestarias de ayuda a la implantación de elevadores para los edificios que aún no dispongan de los mismos priorizando aquellos ocupados por personas de mayor edad.',tags: ['Servicios públicos']}, 
	{id:107, uncollapsed: false, name: 'Transporte.', description: 'Transporte. Estudio de la adecuación del servicio a todos los núcleos urbanos, con especial atención a los núcleos diseminados. Gratuidad de estos servicios para sectores especialmente desfavorecidos o con necesidades especiales.',tags: ['Servicios públicos']}, 
	{id:108, uncollapsed: false, name: 'Interconexión con La Vega.', description: 'Interconexión con La Vega. Estudio de interconexión con otros pueblos del entorno a fin de fomentar el consumo en nuestra tierra',tags: ['Turismo', 'Servicios públicos']}, 
	{id:109, uncollapsed: false, name: 'Colocación de puntos completos de recogida de basura.', description: 'Colocación de puntos completos de recogida de basura. En la mayoría de ellos faltan contenedores para vidrio. Además, estudiaremos la posibilidad de incrementar la frecuencia de recogida y la mejora de la accesibilidad de los contenedores actuales.',tags: ['Servicios públicos']}, 
	{id:110, uncollapsed: false, name: 'Administración electrónica.', description: 'Administración electrónica. Impulsaremos un considerable ahorro en los medios técnicos con la implantación de la informática libre. Haremos un plan de migración a software y hardware libre que incluya un plan especial de formación para la transición tecnológica. Basta de depender de grandes multinacionales y empresas que no mejoren nuestros servicios públicos.',tags: ['Servicios públicos']}, 
	{id:111, uncollapsed: false, name: 'Opendata.', description: 'Opendata. La información del ayuntamiento estará publicada en formato libre, accesible y abierto mediante estándares conocidos y explotables por el público en general. De especial interés para periodistas e investigadores, la información pública debe estar disponible para su dueño legítimo, la ciudadanía en general.',tags: ['Servicios públicos']}, 
	{id:112, uncollapsed: false, name: 'Apuesta por la gestión directa.', description: 'Apuesta por la gestión directa. Debido a que existen necesidades transversales a todas las áreas de los servicios municipales, haremos una auditoría cuyo objetivo sea determinar las inversiones necesarias para reducir la factura de subcontratación a empresas para la prestación de servicios públicos. Esta auditoría vendrá acompañado de un plan de amortización de las inversiones y de retorno de la inversión. Será publicado en formato abierto y accesible.',tags: ['Servicios públicos']}, 
];

export default data;



